body {
  margin: 0px;
  padding: 0px;
  font-family: 'Rubik',sans-serif;
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
  direction: rtl;
  color: blue;
  max-width: 300px;
  margin: auto;
}

button{
  outline: none;
  font-weight: bold;
  transition: 0.5s;
  font-size: 14px;
}

a:link{
  text-decoration: none;
}

h3{
  margin: 5px;
}

p{
  margin: 0px;
}

#logoH1{
  margin: 0px;
}

hr {
  border: 0;
  height: 2px;
  width: 200px;
  color: blue;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), blue, rgba(0, 0, 0, 0));
}

#hr2{
  margin-bottom: 100px;
}

.select{
  color: blue;
  border: 1.5px solid blue;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
  width: 250px;
  height: 30px;
  outline: none;
  background-color: white;
}

#select2{
  margin-top: 5px;
}

#addInput{
  width: 237px;
  height: 17px; 
  margin-bottom: 3px; 
}
#addInput::placeholder{
  color: blue;
}

#nameError{
  color: red;
  width: 250px;
  height: 12px;
  margin: 0 auto;
  text-align: right;
  font-size: 12px;
}

#roomButtonsDiv{
  display: inline-block;
}

.roomButton{
  background-color: white;
  height: 70px;
  width: 70px;
  border-radius: 50px;
  border: 2px solid ;
  margin: 5px;
  overflow: hidden;
}

#roomDiv{
  position: absolute;
  top:250px ;
  margin-right: auto;
  margin-left: auto;
  left: 0px;
  right: 0px;
  max-width: 300px;
}

.roomButtons{
  color: blue;
  background-color: white;
  height: 60px;
  width: 70px;
  border-radius: 10px;
  border: 1.5px solid blue;
  margin: 15px 5px 5px 5px ;
}
.roomButtons:hover{
  color: white;
  background-color: blue;
  border: none;
}

.itemButtons{
  vertical-align: top;
  border: none;
  margin: 5px;
  background-color: white;
}

#selectItens{
  vertical-align: top;
  width: 190px;
  margin-left: 0px;
  border-radius:0px 5px 5px 0px ;
}

#addItem{
  color: white;
  background-color: blue;
  height: 30px;
  width: 60px;
  border-radius:5px 0px 0px 5px ;
  border: 1.5px solid blue;
  padding: 0px;
  margin-top: 10px;
}


#AirConditionerOff{
  width: 100px;
}

#BoilerOff{
  height: 100px;
}

#StereoOff{
  width: 100px;
}

#LightOff{
  height: 50px;
}